@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body {
  margin: 0;
  padding: 0;
  width: 100%;
  background-color: #313639;
  overflow-x: hidden;
}

.secContainer{
  height: 100%;
  width: 100%;
  display: flex;
}

.UNTside{
  width: 50%;
  height: 100%;
  background-image: url("../public/unt.jpg");
  background-size: 1550px;
  background-position: 38%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: rgb(223, 223, 223);
  font-family: 'Roboto', sans-serif;
}

.UNTside h1{
  font-size: 80px;
  margin-bottom: 65px;
}

.UNTside h2{
  max-width: 60%;
  text-align: justify;
  font-size: 20px;
  line-height: 32px;
}

.UTDside{
  width: 50%;
  height: 100%;
  background-image: url("../public/utd.jpg");
  background-size: 1550px;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: rgb(223, 223, 223);
  font-family: 'Roboto', sans-serif;
}

.UTDside h1{
  font-size: 80px;
  margin-bottom: 65px;
}

.UTDside h2{
  max-width: 60%;
  text-align: justify;
  font-size: 20px;
  line-height: 32px;
}

.Blocks {
  display: flex;
  width: 450px;
  height: 800px;
  border-radius: 25px;
  filter: drop-shadow(3px 0px 4px #0000002F);
  background-color: #292e31;
  border: 1px solid #464e53;
  animation: blocks-decrese 0.25s forwards ease;
  margin: 40px;
  color: rgb(223, 223, 223);
  font-family: 'Roboto', sans-serif;
  justify-content: center;
  align-items: center;
}

.Blocks a {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Blocks h1{
  padding: 15px;
}

.Blocks h2{
  font-size: 15px;
  padding: 15px;
  text-align: justify;
}

.Blocks h3{
  font-size: 15px;
  margin-bottom: 0px;
  color: rgba(223, 223, 223, 0.781);
  padding: 20px;
  padding-bottom: 0px;

}

.Blocks img{
  width: 75%;
  border-radius: 10px;
}

.Blocks:hover {
  animation: block-expand 0.25s forwards ease;
}

#Blocks1:hover #Blocks2 #Blocks3 {
  background-color: red;
}

.aboutPhoto{
  position: relative;
  top: 46%;
  left: 10%;
}

.fourthSection h1{
  color: rgb(223, 223, 223);
  font-size: 100px;
  position: absolute;
  font-family: 'Roboto', sans-serif;
  filter: drop-shadow(-2px 2px 5px black);
  left:50%;
  top: 20%;
}

.fourthSection h3{
  color: rgb(223, 223, 223);
  position: absolute;
  left: 42%;
  font-family: 'Roboto', sans-serif;
  text-align: justify;
  line-height: 47px;
  max-width: 40%;
  font-size: 30px;
  top: 35%;
}

.fourthSection a{
  position: absolute;
  left: 57%;
  display: flex;
  top:68%;
  width: 200px;
  height: 75px;
  background-color: #313639;
  border-radius: 20px;
  filter: drop-shadow(0px 0px 2px black);
  font-family: 'Roboto', sans-serif;
  color: rgb(223, 223, 223);
  font-size: 30px;
  align-items: center;
  justify-content: center;
}

@keyframes blocks-decrese {
  0% {
    transform: scale(120%);
  }

  100% {
    transform: scale(100%);
  }
}

@keyframes block-expand {
  0% {
    transform: scale(100%);
  }

  100% {
    transform: scale(120%);
  }
}


#fir {
  animation: fadeIn 2s;
}
#sec {
  animation: fadeIn 2s;
}
#sec1 {
  animation: fadeIn 2s;
}
#sec2 {
  animation: fadeIn 2s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

